import React from 'react'

const AndroidButton = () =>
  <button
    style={{
      padding: '15px 20px',
      textDecoration: 'none',
      cursor: 'pointer',
      border: 'none',
      background: '#689F38',
      color: '#FFF',
      fontWeight: 'bold',
      borderRadius: '4px',
      textAlign: 'center',
      fontFamily: 'Roboto, sans-serif',
      fontSize: '1.2rem',
    }}
  >
    Download in Google Play
  </button>

export default AndroidButton



